import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpClientModule } from '@angular/common/http';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ButtonsModule } from '@progress/kendo-angular-buttons';
import { GridModule } from '@progress/kendo-angular-grid';
import { WindowModule, DialogModule } from '@progress/kendo-angular-dialog';
import { DropDownsModule } from '@progress/kendo-angular-dropdowns';
import { GSPHttpModule } from '@farris/ide-devkit/services';
import { FarrisDialogModule } from '@farris/ui';
import { MetadataSelectModule } from '@gsp-lcm/metadata-selector';
import { AifBeActionDesignerComponent } from './aif-be-action-designer/aif-be-action-designer.component';

@NgModule({
  imports: [
    CommonModule,
    HttpClientModule,
    FormsModule,
    ReactiveFormsModule,
    ButtonsModule,
    GridModule,
    WindowModule,
    DialogModule,
    DropDownsModule,
    GSPHttpModule,
    FarrisDialogModule,
    MetadataSelectModule
  ],
  declarations: [AifBeActionDesignerComponent],
  entryComponents: [AifBeActionDesignerComponent]
})
export class AifBeActionModule {
}



