import { Component, OnInit, ComponentFactoryResolver, Injector, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { CacheService } from '@farris/ide-devkit/services';
import { BsModalService } from '@farris/ui';
import { MetadataDto, GSPMetadataService, SelectMetadataComponent } from '@gsp-lcm/metadata-selector';
import { AifBeActionEntity } from '../aif-be-action-entity';
import { AifBeActionService } from '../aif-be-action.service';
import { BizActionType } from '../BizActionType';

@Component({
  selector: 'app-aif-be-action-designer',
  templateUrl: './aif-be-action-designer.component.html',
  styleUrls: ['./aif-be-action-designer.component.css'],
  providers: [AifBeActionService]
})
export class AifBeActionDesignerComponent implements OnInit {
  bizActionTypes: Array<any> = new Array<any>();
  selectedContext: any = { value: null, text: null };
  disabled: boolean = false;
  toDesignerBeAction: AifBeActionEntity;
  metadataDto: MetadataDto;
  title = 'app';
  private _metadataUri: string;
  set metadataUri(metadataUri: string) {
    this._metadataUri = metadataUri;
  }

  constructor(private lcmService: GSPMetadataService, private cache: CacheService, private beActionService: AifBeActionService,
    private modalService: BsModalService, private resolver: ComponentFactoryResolver, private injector: Injector) {
  }

  ngOnInit() {
    let fullname: string = this._metadataUri.substring(this._metadataUri.lastIndexOf('/') + 1);
    let path: string = this._metadataUri.substring(1, this._metadataUri.lastIndexOf('/'));
    this.lcmService.LoadMetadata(fullname, path).subscribe(data => {
      this.metadataDto = data;
      this.loadMetadata(this.metadataDto);

      this.bizActionTypes.push({ value: 'Creation', text: 'Creation' });
      this.bizActionTypes.push({ value: 'Writeback', text: 'Writeback' });

      if (this.toDesignerBeAction && this.toDesignerBeAction.BizActionType) {
        if (this.toDesignerBeAction.BizActionType == BizActionType.Creation) {
          this.selectedContext = this.bizActionTypes[0];
        }
        else if (this.toDesignerBeAction.BizActionType == BizActionType.Writeback) {
          this.selectedContext = this.bizActionTypes[1];
        }
      }
      else {
        this.selectedContext = this.bizActionTypes[0];
      }
    }, error => {
      alert("加载元数据失败。")
    });
  }

  saveMetadata() {
    this.saveMetadataService().subscribe(() => {
      alert("保存成功");
    }, error => {
      alert("保存失败")
    });
  }

  saveMetadataService() {
    this.toDesignerBeAction.BizActionType = this.selectedContext.value;
    let beAction: string = JSON.stringify(this.toDesignerBeAction);
    let entity: any = {};
    entity.Entity = beAction;
    this.metadataDto.content = JSON.stringify(entity);

    return this.lcmService.SaveMetadata(this.metadataDto, this.cache.get("sessionId"));
  }

  publishMetadata(): void {
    this.saveMetadataService().subscribe(() => {
      this.lcmService.GetProjectInfo(this.metadataDto.relativePath).subscribe(data => {
        this.toDesignerBeAction.MetadataID = this.metadataDto.id;
        this.toDesignerBeAction.MetadataPackageName = data.metadataPackageInfo.name;
        this.lcmService.GetGspProjInfo(data.projectPath).subscribe(data => {
          this.toDesignerBeAction.SuName = data.serviceUnitCode;
          this.beActionService.publishBeAction(this.toDesignerBeAction).subscribe(() => {
            alert("发布成功。");
            this.disabled = true;
          }, error => {
            let errorString: string = error.error ? error.error.Message : error.statusText;
            alert("发布失败。\n" + errorString);
          });
        })
      });
    });
  }

  unPublishMetadata(): void {
    this.beActionService.unPublishBeAction(this.metadataDto.id).subscribe(() => {
      alert("撤销发布成功。");
      this.disabled = false;
    }, error => {
      let errorString: string = error.error ? error.error.Message : error.statusText;
      alert("撤销发布失败。\n" + errorString);
    });
  }

  private loadMetadata(dto: MetadataDto) {
    this.metadataDto = dto;
    if (this.metadataDto.content && this.metadataDto.content != "") {
      let entity = JSON.parse(this.metadataDto.content);
      if (entity.Entity != "null") {
        this.toDesignerBeAction = JSON.parse(entity.Entity);
        if (this.toDesignerBeAction.Name == null) {
          this.toDesignerBeAction.Name = this.metadataDto.name;
        }
      }
      else {
        this.toDesignerBeAction = new AifBeActionEntity();
        this.toDesignerBeAction.Name = this.metadataDto.name;
      }

      this.beActionService.isBeActionPublished(this.metadataDto.id).subscribe(data => {
        this.disabled = data.value;
      });
    }
  }

  selectMetadata(typeName: string) {
    const compFactory = this.resolver.resolveComponentFactory(SelectMetadataComponent);
    const compRef = compFactory.create(this.injector);
    compRef.instance.metadataPath = this.metadataDto.relativePath;
    compRef.instance.typeName = typeName;
    compRef.instance.selectedMetadata.subscribe($event => {
      if (typeName == "GSPBusinessEntity") {
        this.toDesignerBeAction.BeMetadataID = $event.metadata.id;
        this.toDesignerBeAction.BeType = $event.metadata.code;
        this.toDesignerBeAction.BeMetadataPackageName = $event.packageHeader.name;
      }
      else if (typeName == "CommonComponent") {
        this.toDesignerBeAction.BeActionAgentComponentId = $event.metadata.id;
      }
    });
    const modalConfig = {
      title: '元数据选择',
      width: 882,
      height: 573,
      showButtons: false
    };
    const dialog = this.modalService.show(compRef, modalConfig);

    compRef.instance.closeModalEmitter.subscribe($event => {
      dialog.close();
    });
  }

  valueChange(value: any): void {
    if (value == "Creation") {
      this.selectedContext = this.bizActionTypes[0];
    }
    else if (value == "Writeback") {
      this.selectedContext = this.bizActionTypes[1];
    }
  }

  clearBeActionAgentComponentId() {
    this.toDesignerBeAction.BeActionAgentComponentId = null;
  }

  clearbeMetadata() {
    this.toDesignerBeAction.BeMetadataID = null;
    this.toDesignerBeAction.BeType = null;
    this.toDesignerBeAction.BeMetadataPackageName = null;
  }

  clearBeActionAgentComponentIdable: boolean = false;
  setClearBeActionAgentComponentIdable(display: boolean) {
    if (this.toDesignerBeAction.BeActionAgentComponentId != null) {
      this.clearBeActionAgentComponentIdable = display;
    }
    else {
      this.clearBeActionAgentComponentIdable = false;
    }
  }

  clearBeMetadataIDable: boolean = false;
  setClearBeMetadataIDable(display: boolean) {
    if (this.toDesignerBeAction.BeMetadataID != null) {
      this.clearBeMetadataIDable = display;
    }
    else {
      this.clearBeMetadataIDable = false;
    } 
  }
}



