import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/observable';
import { HttpService } from '@farris/ide-devkit/services';

@Injectable()
export class AifBeActionService {
  private baseUrl = '/api/dev/sys/v1.0/BeActionEntities';

  constructor(private http: HttpService) { }

  publishBeAction(item: any): Observable<any> {
    return this.http.post(this.baseUrl, item);
  }

  unPublishBeAction(metadataId: string): Observable<any> {
    let param: any = {
      "parameters": metadataId
    }

    const url = ` ${this.baseUrl}/UnPublishBeAction`;
    return this.http.post(url, param);
  }

  isBeActionPublished(metadataId: string): Observable<any> {
    const url = ` ${this.baseUrl}/IsPublished(metadataId='${metadataId}')`;
    return this.http.get(url);
  }
}



