import { ComponentFactoryResolver, Injector, Type } from '@angular/core';
import { GSP, Package } from '@farris/ide-devkit';
import { AifBeActionPackageView } from './views';
import { AifBeActionDesignerComponent } from './views/aif-be-action-designer/aif-be-action-designer.component';

export class AifBeActionPackage extends Package {
  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    this.gsp.workspace.addOpener((uri: string) => this.openBeAction(uri));
  }

  openBeAction(uri) {
    if (uri.endsWith('.aifac')) {
      const pkgView = new AifBeActionPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as AifBeActionDesignerComponent;
      currentElement.metadataUri = uri;
      return {
        getTitle: () => uri.substring(uri.lastIndexOf('/') + 1),
        getElement: () => pkgView.getElement()
      };
    }
  }
}


